import { sql } from "drizzle-orm";
import { database } from "./client";

export const initializeDatabase = async (): Promise<void> => {
  await database.run(sql`
    CREATE TABLE IF NOT EXISTS users (
      id TEXT PRIMARY KEY NOT NULL,
      name TEXT NOT NULL,
      email TEXT NOT NULL,
      password_hash TEXT NOT NULL,
      api_credits INTEGER NOT NULL DEFAULT 0,
      created_at TEXT NOT NULL,
      updated_at TEXT NOT NULL
    );
  `);

  await database.run(sql`CREATE UNIQUE INDEX IF NOT EXISTS users_email_unique ON users(email);`);

  await database.run(sql`
    CREATE TABLE IF NOT EXISTS password_reset_tokens (
      id TEXT PRIMARY KEY NOT NULL,
      user_id TEXT NOT NULL,
      token_hash TEXT NOT NULL,
      expires_at TEXT NOT NULL,
      used_at TEXT,
      created_at TEXT NOT NULL
    );
  `);

  await database.run(sql`CREATE INDEX IF NOT EXISTS password_reset_tokens_user_id_idx ON password_reset_tokens(user_id);`);
  await database.run(
    sql`CREATE UNIQUE INDEX IF NOT EXISTS password_reset_tokens_token_hash_unique ON password_reset_tokens(token_hash);`,
  );

  await database.run(sql`
    CREATE TABLE IF NOT EXISTS passkeys (
      id TEXT PRIMARY KEY NOT NULL,
      user_id TEXT NOT NULL,
      credential_id TEXT NOT NULL,
      credential_public_key TEXT NOT NULL,
      counter INTEGER NOT NULL DEFAULT 0,
      device_type TEXT NOT NULL,
      backed_up INTEGER NOT NULL DEFAULT 0,
      transports_json TEXT,
      created_at TEXT NOT NULL,
      last_used_at TEXT
    );
  `);

  await database.run(sql`CREATE INDEX IF NOT EXISTS passkeys_user_id_idx ON passkeys(user_id);`);
  await database.run(sql`CREATE UNIQUE INDEX IF NOT EXISTS passkeys_credential_id_unique ON passkeys(credential_id);`);

  await database.run(sql`
    CREATE TABLE IF NOT EXISTS api_keys (
      id TEXT PRIMARY KEY NOT NULL,
      user_id TEXT NOT NULL,
      name TEXT NOT NULL,
      key_hash TEXT NOT NULL,
      prefix TEXT NOT NULL,
      masked_key TEXT NOT NULL,
      status TEXT NOT NULL DEFAULT 'active',
      last_used_at TEXT,
      created_at TEXT NOT NULL
    );
  `);

  await database.run(sql`CREATE INDEX IF NOT EXISTS api_keys_user_id_idx ON api_keys(user_id);`);
  await database.run(sql`CREATE UNIQUE INDEX IF NOT EXISTS api_keys_key_hash_unique ON api_keys(key_hash);`);

  await database.run(sql`
    CREATE TABLE IF NOT EXISTS video_tasks (
      id TEXT PRIMARY KEY NOT NULL,
      user_id TEXT NOT NULL,
      model TEXT NOT NULL,
      prompt TEXT NOT NULL,
      image_url TEXT,
      aspect_ratio TEXT NOT NULL,
      duration_seconds INTEGER NOT NULL,
      credits_cost INTEGER NOT NULL,
      status TEXT NOT NULL,
      provider_task_id TEXT,
      output_video_url TEXT,
      error_message TEXT,
      created_at TEXT NOT NULL,
      updated_at TEXT NOT NULL,
      completed_at TEXT
    );
  `);

  await database.run(sql`CREATE INDEX IF NOT EXISTS video_tasks_user_id_idx ON video_tasks(user_id);`);
  await database.run(sql`CREATE INDEX IF NOT EXISTS video_tasks_created_at_idx ON video_tasks(created_at);`);

  await database.run(sql`
    CREATE TABLE IF NOT EXISTS credit_ledger (
      id TEXT PRIMARY KEY NOT NULL,
      user_id TEXT NOT NULL,
      delta_credits INTEGER NOT NULL,
      balance_after INTEGER NOT NULL,
      reason TEXT NOT NULL,
      reference_id TEXT,
      metadata_json TEXT,
      created_at TEXT NOT NULL
    );
  `);

  await database.run(sql`CREATE INDEX IF NOT EXISTS credit_ledger_user_id_idx ON credit_ledger(user_id);`);

  await database.run(sql`
    CREATE TABLE IF NOT EXISTS payments (
      id TEXT PRIMARY KEY NOT NULL,
      user_id TEXT NOT NULL,
      provider TEXT NOT NULL,
      provider_payment_id TEXT NOT NULL,
      status TEXT NOT NULL,
      amount_minor INTEGER NOT NULL,
      currency TEXT NOT NULL,
      credits_purchased INTEGER NOT NULL,
      raw_payload_json TEXT NOT NULL,
      created_at TEXT NOT NULL,
      updated_at TEXT NOT NULL
    );
  `);

  await database.run(sql`CREATE INDEX IF NOT EXISTS payments_user_id_idx ON payments(user_id);`);
  await database.run(sql`CREATE UNIQUE INDEX IF NOT EXISTS payments_provider_payment_unique ON payments(provider_payment_id);`);
};
