import { Database } from "bun:sqlite";
import { drizzle } from "drizzle-orm/bun-sqlite";
import { existsSync, mkdirSync } from "node:fs";
import path from "node:path";
import { env } from "@/server/env";

const resolveSqlitePath = (): string => {
  const configuredPath = env.databaseUrl;
  const filePath = configuredPath.startsWith("file:") ? configuredPath.replace("file:", "") : configuredPath;
  if (path.isAbsolute(filePath)) {
    return filePath;
  }
  return path.resolve(process.cwd(), filePath);
};

const sqlitePath = resolveSqlitePath();
const sqliteDirectory = path.dirname(sqlitePath);
if (!existsSync(sqliteDirectory)) {
  mkdirSync(sqliteDirectory, { recursive: true });
}

const sqlite = new Database(sqlitePath, { create: true });
sqlite.exec("PRAGMA foreign_keys = ON;");
sqlite.exec("PRAGMA journal_mode = WAL;");

export const database = drizzle(sqlite);
export const rawSqlite = sqlite;
